import HelloWorld from './components/HelloWorld'
import Layouts from './components/Layout'
export default {
  name: 'App',
  components: {
    HelloWorld,
    Layouts
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin:0px;
  padding:0px 0px;
}
</style>



