<template>
  <div>
    <el-container>
      <!--顶部导航-->
      <el-header>
        <navtop />
      </el-header>

      <el-container>
        <!--左侧导航-->
        <el-aside width="202px">
          <navleft />
        </el-aside>

        <!--右侧内容区域-->
        <el-main>
          <Navmain />
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>
<script>

import Navtop from './Navtop';
import Navleft from './Navleft';
import Navmain from './Navmain';

export default {
  data(){
    return{
      activeName: '1'
    }
  },
  components:{
    Navtop,
    Navleft,
    Navmain
  }
};
</script>
<style>
.el-header,
.el-footer {
  background-color: #b3c0d1;
  color: #333;
  text-align: center;
  line-height: 60px;
}

.el-aside {
  background-color: #d3dce6;
  color: #333;
  text-align: center;
  height:100%;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  text-align: center;
  height:100%;
}

body > .el-container {
  margin-bottom: 40px;
}





</style>



